import Vue from 'vue'

export default {
	data() {
		return {
			code: null
		};
	},
	onLoad() {
		var that = this
		wx.login({
			success(res) {
				// console.log(res);
				that.code = res.code 
			}, fail(err){
				uni.showModal({
					content: '微信授权失败，请退出重进'
				})
			}
		})
	},
	methods: {
		Tourist(){
			uni.reLaunch({
				url: '../blank/blank'
			})
		},
		viewServiec(){
			uni.navigateTo({
				url:  "../user-service/user-service"
			})
		},
		bindGetUserInfo(e){
			var that = this
			uni.showLoading({
				title: '登陆中...'
			})
			Vue.prototype.UserInfo = e.detail.userInfo
			uni.setStorage({
				key: 'UserInfo',
				data: e.detail.userInfo
			})
			var iv = e.detail.iv
			var encryptedData = e.detail.encryptedData
			uni.request({
				method: 'POST',
				url: that.Server + 'user/login',
				data: {
					"code": that.code,
					"encryptedData": encryptedData,
					"iv": iv
				},
				success(res) {
					if(res.data.code == 0){
						uni.setStorage({
							key: "Token",
							data: res.data.data.token
						})
						console.log(res.data.data.token);
						Vue.prototype.Token = res.data.data.token
						uni.reLaunch({
							url: '../blank/blank?scene=' + that.MerchantId
						})
					} else {
						uni.hideLoading()
						uni.showModal({
							content: res.data.msg
						})
					}
				},
				fail(err) {
					uni.hideLoading()
					uni.showModal({
						content: '登录异常，请退出重进'
					})
				}
			})
		},
		// goIndex(){
